<template>
  <v-container>
    <v-row>
      <v-col cols="12" lg="6">
        <v-card class="text-center" :loading="!data">
          <v-card-title class="headline font-weight-thin justify-center">
            {{ $t('Commissions last 8 weeks') }}
          </v-card-title>
          <v-card-text>
            <v-sheet color="rgba(0, 0, 0, .15)">
              <v-sparkline
                :value="data ? data.commissions_by_week : Array(8).fill(0)"
                :color="chartLineColor"
                height="150"
                padding="24"
                stroke-linecap="round"
                line-width="2"
                smooth="5"
              >
                <template v-slot:label="item">
                  {{ short(item.value) }}
                </template>
              </v-sparkline>
            </v-sheet>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12" lg="6" class="text-center">
        <v-card :loading="!data">
          <v-card-title class="headline font-weight-thin justify-center">
            {{ $t('Referred users') }}
          </v-card-title>
          <v-card-text>
            <v-row>
              <v-col cols="12" md="6">
                <v-progress-circular
                  :rotate="-90"
                  :size="200"
                  :width="30"
                  :value="data ? 100 * data.referred_last_month / (data.referred_last_month + data.referred_this_month) : 0"
                  color="primary"
                >
                  <span class="headline">{{ data ? data.referred_last_month : 0 }}</span>
                </v-progress-circular>
                <v-subheader class="title font-weight-thin justify-center mt-3">
                  {{ $t('last month') }}
                </v-subheader>
              </v-col>
              <v-col cols="12" md="6" class="text-center">
                <v-progress-circular
                  :rotate="-90"
                  :size="200"
                  :width="30"
                  :value="data ? 100 * data.referred_this_month / (data.referred_last_month + data.referred_this_month) : 0"
                  color="primary"
                >
                  <span class="headline">{{ data ? data.referred_this_month : 0 }}</span>
                </v-progress-circular>
                <v-subheader class="title font-weight-thin justify-center mt-3">
                  {{ $t('this month') }}
                </v-subheader>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" class="text-center">
        <v-card :loading="!data">
          <v-card-title class="headline font-weight-thin justify-center">
            {{ $t('Commissions by status') }}
          </v-card-title>
          <v-card-text>
            <v-row>
              <v-col cols="12" md="4">
                <v-progress-circular
                  :rotate="-90"
                  :size="200"
                  :width="30"
                  :value="data ? 100 * data.pending_commissions_total / totalCommissionsTotal : 0"
                  color="primary"
                >
                  <span class="headline">{{ short(data ? data.pending_commissions_total : 0) }}</span>
                </v-progress-circular>
                <v-subheader class="title font-weight-thin justify-center mt-3">
                  {{ $t('Pending') }}
                </v-subheader>
              </v-col>
              <v-col cols="12" md="4" class="text-center">
                <v-progress-circular
                  :rotate="-90"
                  :size="200"
                  :width="30"
                  :value="data ? 100 * data.approved_commissions_total / totalCommissionsTotal : 0"
                  color="primary"
                >
                  <span class="headline">{{ short(data ? data.approved_commissions_total : 0) }}</span>
                </v-progress-circular>
                <v-subheader class="title font-weight-thin justify-center mt-3">
                  {{ $t('Approved') }}
                </v-subheader>
              </v-col>
              <v-col cols="12" md="4" class="text-center">
                <v-progress-circular
                  :rotate="-90"
                  :size="200"
                  :width="30"
                  :value="data ? 100 * data.rejected_commissions_total / totalCommissionsTotal : 0"
                  color="primary"
                >
                  <span class="headline">{{ short(data ? data.rejected_commissions_total : 0) }}</span>
                </v-progress-circular>
                <v-subheader class="title font-weight-thin justify-center mt-3">
                  {{ $t('Rejected') }}
                </v-subheader>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" lg="12">
        <v-card class="text-center" :loading="!data">
          <v-card-title class="headline font-weight-thin justify-center">
            {{ $t('Commissions by type') }}
          </v-card-title>
          <v-card-text>
            <v-simple-table>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">{{ $t('Type') }}</th>
                    <th class="text-right">{{ $t('Count') }}</th>
                    <th class="text-right">{{ $t('Total') }}</th>
                  </tr>
                </thead>
                <tbody>
                  <template v-if="data">
                    <tr v-for="item in data.commissions_by_type" :key="item.type">
                      <td class="text-left">{{ item.title }}</td>
                      <td class="text-right">{{ integer(item.commissions_count) }}</td>
                      <td class="text-right">{{ decimal(item.commissions_total) }}</td>
                    </tr>
                  </template>
                  <template v-else>
                    <tr v-for="(v,i) in Array(3).fill(0)" :key="i">
                      <td colspan="3">
                        <v-skeleton-loader type="text" />
                      </td>
                    </tr>
                  </template>
                </tbody>
              </template>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios'
import { integer, decimal, short } from '~/plugins/format'

export default {
  middleware: ['auth', 'verified', '2fa_passed', 'admin'],

  metaInfo () {
    return { title: this.$t('Dashboard') + ' ' + this.$t('Users') }
  },

  computed: {
    theme () {
      const mode = this.$vuetify.theme.isDark ? 'dark' : 'light'
      return this.$vuetify.theme.themes[mode]
    },
    chartLineColor () {
      return this.theme.primary
    },
    totalCommissionsTotal () {
      return this.data
        ? this.data.pending_commissions_total + this.data.approved_commissions_total + this.data.rejected_commissions_total
        : 0
    }
  },

  data () {
    return {
      data: null
    }
  },

  async created () {
    const { data } = await axios.get('/api/admin/dashboard/affiliates')

    this.data = data
  },

  methods: {
    integer,
    decimal,
    short
  }
}
</script>
